<template>
<div class="image-container set-full-height" style="background-color: #EAF4F4">
    <a href="https://izzz.io/">
        <div class="logo-container">
            <div class="logo">
                <img src="@/assets/img/new_logo.png">
            </div>
        </div>
    </a>
    <div class="container">
        <div class="row">
            <div class="col-sm-8 col-sm-offset-2">
                <div class="wizard-container">
                    <div class="card wizard-card" data-color="orange" id="wizardProfile">
                        <form action="" method="">
                            <div class="wizard-header">
                                <h3>
                                    <b>MANAGE</b> YOUR PROFILES
                                </h3>
                            </div>
                            <div class="wizard-navigation">
                                <ul class="nav nav-pills">
                                    <li @click="handleSelectTab(1)" class="pointer__tab" style="width: 33.3333%; padding: 9px 15px; text-align: center; color: #fff;">INFO</li>
                                    <li @click="handleSelectTab(2)" class="pointer__tab" style="width: 33.3333%; padding: 9px 15px; text-align: center; color: #fff;">ORACLES</li>
                                    <li @click="handleSelectTab(3)" class="pointer__tab" style="width: 33.3333%; padding: 9px 15px; text-align: center; color: #fff;">TRANSFER</li>
                                </ul>
                                <div class="moving-tab" :style="`padding: 9px; width: 250px; transform: translate3d(${valueForTranslate}px, 0px, 0px); transition: all 0.3s ease-out 0s; font-size: 14px;`">{{tabsName[currentTab - 1]}}</div>
                            </div>
                            <div class="tab-content">
                                <div class="tab-pane" :class="{ active: currentTab == 1 }" id="about">
                                    <div class="row">
                                        <div class="col-sm-2">
                                            <div class="picture-container">
                                                <div class="user-photo">
                                                    <img v-if="linkPhoto" :src="linkPhoto" class="picture-src" id="wizardPicturePreview" title="" />
                                                    <img v-else src="@/assets/img/default-avatar.png" class="picture-src" id="wizardPicturePreview" title="" />
                                                </div>
                                            </div>
                                        </div>
                                        <div class="col-sm-9">
                                            <div class="form-group">
                                                <label>Full Name</label>
                                                <div class="form-control">{{ fullName }}</div>
                                            </div>
                                        </div>
                                        <div class="col-sm-1">
                                            <div class="form-group">
                                                <label>KYC</label>
                                                <div class="form-control">
                                                    <i v-if="KYC" class="fa fa-check-circle"></i>
                                                    <i v-else class="fa fa-times"></i>
                                                </div>
                                            </div>
                                        </div>
                                        <div v-for="obj, index in metrics" :key="index" class="col-sm-4">
                                            <div class="form-group">
                                                <label>{{ obj.label }}</label>
                                                <div class="form-control">{{ obj.result }}</div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                                <div class="tab-pane" :class="{ active: currentTab == 2 }" id="account">
                                    <h4 v-if="!confirmations.length && !nameMetrics.length" class="info-text">So far, no oracle changes the metrics on this profile</h4>
                                    <div v-if="confirmations.length">
                                        <h4 class="info-text">Confirmed KYC</h4>
                                        <div class="row">
                                            <div class="col-sm-4">
                                                <div class="form-group">
                                                    <label>Metric Name</label>
                                                </div>
                                            </div>
                                            <div class="col-sm-4">
                                                <div class="form-group">
                                                    <label>Metric Label</label>
                                                </div>
                                            </div>
                                            <div class="col-sm-4">
                                                <div class="form-group">
                                                    <label>Oracle Label</label>
                                                </div>
                                            </div>
                                        </div>
                                        <div class="row" v-for="obj, index in confirmations" :key="index">
                                            <div class="col-sm-4">
                                                <div class="form-group" style="margin-bottom: 4px;">
                                                    <div>{{obj.name}}</div>
                                                </div>
                                            </div>
                                            <div class="col-sm-4">
                                                <div class="form-group" style="margin-bottom: 4px;">
                                                    <div>{{obj.label}}</div>
                                                </div>
                                            </div>
                                            <div class="col-sm-4">
                                                <div class="form-group" style="margin-bottom: 4px;">
                                                    <div >{{obj.oracleLabel}}</div>
                                                </div>
                                            </div>
                                            <div class="col-sm-12">
                                                <div class="form-group" style="text-align: center;">
                                                    <div class="form-control">{{obj.oracle}}</div>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                    <div v-if="nameMetrics.length">
                                        <h4 class="info-text">Other oracles</h4>
                                        <div class="row">
                                            <div class="col-sm-4">
                                                <div class="form-group">
                                                    <label>Metric Name</label>
                                                </div>
                                            </div>
                                            <div class="col-sm-4">
                                                <div class="form-group">
                                                    <label>Metric Label</label>
                                                </div>
                                            </div>
                                            <div class="col-sm-4">
                                                <div class="form-group">
                                                    <label>Oracle Label</label>
                                                </div>
                                            </div>
                                        </div>
                                        <div class="row" v-for="obj, index in nameMetrics" :key="index">
                                            <div class="col-sm-4">
                                                <div class="form-group" style="margin-bottom: 4px;">
                                                    <div>{{obj.name}}</div>
                                                </div>
                                            </div>
                                            <div class="col-sm-4">
                                                <div class="form-group" style="margin-bottom: 4px;">
                                                    <div>{{obj.label}}</div>
                                                </div>
                                            </div>
                                            <div class="col-sm-4">
                                                <div class="form-group" style="margin-bottom: 4px;">
                                                    <div >{{obj.oracleLabel}}</div>
                                                </div>
                                            </div>
                                            <div class="col-sm-12">
                                                <div class="form-group" style="text-align: center;">
                                                    <div class="form-control">{{obj.oracle}}</div>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                                <div class="tab-pane" :class="{ active: currentTab == 3 }" id="address">
                                    <div v-if="isTransmittedProfile" class="row">
                                        <div class="col-sm-12">
                                            <h4 class="info-text">Profile transferred to another address</h4>
                                        </div>
                                        <div class="col-sm-12" style="text-align: center">
                                            <div class="form-group">
                                                <input @click="handleCancelTransferTransmittedProfile()" type='button' class='btn btn-next btn-fill btn-warning btn-wd btn-sm' name='next' value='Cancel' />
                                            </div>
                                        </div>
                                    </div>
                                    <div v-else-if="isAcceptedProfile" class="row">
                                        <div class="col-sm-12">
                                            <h4 class="info-text">This profile has been sent to your address</h4>
                                        </div>
                                        <div class="col-sm-3 col-sm-offset-2" style="text-align: center">
                                            <div class="form-group">
                                                <input @click="handleAcceptOwnership()" type='button' class='btn btn-next btn-fill btn-warning btn-wd btn-sm' name='next' value='Accept Ownership' />
                                            </div>
                                        </div>
                                        <div class="col-sm-3 col-sm-offset-2" style="text-align: center">
                                            <div class="form-group">
                                                <input @click="handleCancelTransferAcceptedProfile()" type='button' class='btn btn-next btn-fill btn-warning btn-wd btn-sm' name='next' value='Cancel' />
                                            </div>
                                        </div>
                                    </div>
                                    <div v-else class="row">
                                        <div class="col-sm-12">
                                            <h4 class="info-text">Enter the address of the new owner</h4>
                                        </div>
                                        <div class="col-sm-8 col-sm-offset-1">
                                            <div class="form-group">
                                                <input v-model="newOwner" type="text" class="form-control" placeholder="Address 0x...">
                                            </div>
                                        </div>
                                        <div class="col-sm-2">
                                            <div class="form-group">
                                                <input @click="handleTransferProfile()" type='button' class='btn btn-next btn-fill btn-warning btn-wd btn-sm' name='next' value='Transfer' />
                                            </div>
                                        </div>
                                        <div class="col-sm-2 col-sm-offset-1">
                                            <div class="form-group">
                                                <input @click="handleClickDeleteProfile()" type='button' class='btn btn-next btn-fill btn-danger btn-wd btn-sm' name='next' value='Delete Profile' />
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </form>
                    </div>
                </div> 
            </div>
        </div>
    </div>
    <div class="footer">
        <div class="container">
        </div>
    </div>
    <div class="fixed-plugin" style="position: fixed; top: 50px; right: 50px;">
        <div class="dropdown open">
            <div @click="handleOpenSetting()" href="#" data-toggle="dropdown" aria-expanded="false">
                <i class="fa fa-cog fa-2x"></i>
            </div>
            <ul v-if="openSetting" class="dropdown-menu" style="left: -330px"> 
                <li v-if="profilesAndNames.length" class="profile" style="font-weight: 900;">
                    Your profiles:
                </li>
                <li :class="{ currentProfile: el.address == currentProfile, pointer: el.address != currentProfile  }" @click="handleSelectProfile(el.address)" v-for="el in profilesAndNames" :key="el.address" class="profile">
                    {{el.address}} {{ el.fullName }} 
                </li>
                <li v-if="acceptedProfilesAndNames.length" class="profile" style="font-weight: 900;">
                    Accepted profiles: 
                </li>
                <li :class="{ currentProfile: el.address == currentProfile, pointer: el.address != currentProfile  }" @click="handleSelectProfile(el.address)" v-for="el in acceptedProfilesAndNames" :key="el.address" class="profile">
                    {{el.address}} {{ el.fullName }}  
                </li>
                <li class="profile">
                    <button @click="handleOpenCreateWindow()" class="btn btn-default btn-fill">Create New Profile</button>
                </li>
            </ul>
        </div>
    </div>
    <div v-if="openCreateWindow" class="premodal">
        <div class="modal-dialog dialog">
            <div class="modal-content">
              <div class="modal-header-1">
                <h5 class="modal-title">Enter your full name</h5>
                <button type="button" class="btn-close" @click="handleCloseCreateWindow()" aria-label="Закрыть">
                    <i class="fa fa-times"></i>
                </button>
              </div>
              <div class="modal-body">
                <div class="mb-3 row">
                    <label for="input1" class="col-sm-2 col-form-label">Full Name:</label>
                    <div class="col-sm-7">
                        <input v-model="newProfile" type="text" class="form-control" id="input1">
                    </div>
                    <div class="col-sm-1">
                        <input @click="handleCreateProfile()" type='button' class='btn btn-next btn-fill btn-warning btn-wd btn-sm' value='CREATE' />
                    </div>
                </div>
              </div>
            </div>
        </div>
    </div>
</div>
</template>

<script>
import { mapActions, mapGetters } from 'vuex';
import { ADDR_DATA_ROUTER, ADDR_MASTER, ADDR_PROFILE_ROUTER } from '@/utils/addresses.js';
import { abiDataRouter, abiMaster, abiProfileContract, abiProfileRouter } from '@/utils/abi.js';
import { fetchContractData, getFirstAddress } from '@/utils/web3.js';
import { methodsByType, converter, getHash } from '@/utils/metricTypes.js';

export default {    
    name: 'MainView',
    async created() {
        await this.GET_PROFILES_ARRAY()
        await this.GET_TRANSMITTED_PROFILES()
        await this.GET_ACCEPTED_PROFILES()
        this.currentProfile = this.PROFILES_ARRAY[0];
        await this.getProfileInfo();
        await this.getNamesByProfiles();
    },
    data() {
        return {
            KYC: false,
            nameMetrics: [],
            metrics: [],
            fullName: '',
            openSetting: false,
            openCreateWindow: false,
            currentProfile: '',
            tabsName: ['INFO', 'ORACLES', 'TRANSFER'],
            currentTab: 1,
            confirmations: [],
            newOwner: '',
            newProfile: '',
            profilesAndNames: [],
            acceptedProfilesAndNames: [],
            linkPhoto: ''
        }
    },
    methods: {
        ...mapActions(['SEND_DATA', 'GET_PROFILES_ARRAY', 'GET_TRANSMITTED_PROFILES', 'GET_ACCEPTED_PROFILES']),
        handleCloseCreateWindow() {
            this.openCreateWindow = false;
        },
        handleOpenCreateWindow() {
            this.openCreateWindow = true;
            this.openSetting = false;
        },
        async handleAcceptOwnership() {
            const data = {
                method: 'acceptProfileOwnership',
                params: [this.currentProfile]
            }
            const user = await getFirstAddress();
            const lengthBefore = await fetchContractData(abiProfileRouter, ADDR_PROFILE_ROUTER, 'getLengthProfilesArray', [user]);
            await this.SEND_DATA(data);
            let pingTimer; 
            let loopCounter = 0;
            pingTimer = setInterval(async () => {
                loopCounter++;
                const lengthAfter = await fetchContractData(abiProfileRouter, ADDR_PROFILE_ROUTER, 'getLengthProfilesArray', [user]);
                if (lengthBefore < lengthAfter || loopCounter > 10) {
                    if(lengthBefore < lengthAfter) {
                        this.$vToastify.success('Data updated successfully', '')
                    } else {
                        this.$vToastify.warning('Maybe something went wrong, track the transaction on etherscan', '')
                    } 
                    clearInterval(pingTimer);
                    await this.GET_PROFILES_ARRAY()
                    await this.GET_TRANSMITTED_PROFILES()
                    await this.GET_ACCEPTED_PROFILES()
                    await this.getProfileInfo();
                    await this.getNamesByProfiles();
                }
            }, 2000)
        },
        async handleClickDeleteProfile() {
            const data = {
                method: 'deleteProfile',
                params: [this.currentProfile]
            }
            const user = await getFirstAddress();
            const lengthBefore = await fetchContractData(abiProfileRouter, ADDR_PROFILE_ROUTER, 'getLengthProfilesArray', [user]);
            await this.SEND_DATA(data)
            let pingTimer; 
            let loopCounter = 0;
            pingTimer = setInterval(async () => {
                loopCounter++;
                const lengthAfter = await fetchContractData(abiProfileRouter, ADDR_PROFILE_ROUTER, 'getLengthProfilesArray', [user]);
                if (lengthBefore > lengthAfter || loopCounter > 15) {
                    if(lengthBefore > lengthAfter) {
                        this.$vToastify.success('Data updated successfully', '')
                    } else {
                        this.$vToastify.warning('Maybe something went wrong, track the transaction on etherscan', '')
                    } 
                    clearInterval(pingTimer);
                    await this.GET_PROFILES_ARRAY()
                    await this.GET_TRANSMITTED_PROFILES()
                    await this.GET_ACCEPTED_PROFILES()
                    this.currentProfile = this.PROFILES_ARRAY[0];
                    await this.getProfileInfo();
                    await this.getNamesByProfiles();
                }
            }, 2000)
        },
        async handleTransferProfile() {
            if(!this.newOwner) {
                this.$vToastify.info('Fill new owner address', 'Validation warning')
            } else {
                const user = await getFirstAddress();
                const lengthBefore = (await fetchContractData(abiProfileRouter, ADDR_PROFILE_ROUTER, 'getTransmittedProfiles', [user])).length;
                const data = {
                    method: 'transferProfileOwnership',
                    params: [this.newOwner, this.currentProfile]
                }
                await this.SEND_DATA(data)
                let pingTimer; 
                let loopCounter;
                pingTimer = setInterval(async () => {
                    loopCounter++;
                    const lengthAfter = (await fetchContractData(abiProfileRouter, ADDR_PROFILE_ROUTER, 'getTransmittedProfiles', [user])).length;
                    if (lengthBefore < lengthAfter || loopCounter > 10) {
                        if(lengthBefore < lengthAfter) {
                            this.$vToastify.success('Data updated successfully', '')
                        } else {
                            this.$vToastify.warning('Maybe something went wrong, track the transaction on etherscan', '')
                        } 
                        clearInterval(pingTimer);
                        await this.GET_PROFILES_ARRAY()
                        await this.GET_TRANSMITTED_PROFILES()
                        await this.GET_ACCEPTED_PROFILES()
                        this.newOwner = '';
                        await this.getNamesByProfiles();
                    }
                }, 2000)
            }
        },
        async getProfileInfo() {
            const profile = this.currentProfile;
            this.fullName = await fetchContractData(abiProfileContract, profile, 'fullName', [])
            this.KYC = await fetchContractData(abiProfileContract, profile, 'KYC', [])
            const lengthConfirmations = await fetchContractData(abiProfileContract, profile, 'getLengthConfirmations', [])
            const lengthMetricNames = await fetchContractData(abiProfileContract, profile, 'getLengthMetricNames', [])
            if(Number(lengthConfirmations)) {
                let confirmations = await fetchContractData(abiProfileContract, profile, 'getSliceConfirmations', [0, lengthConfirmations])
                for(let i = 0; i < confirmations.length; i++) {
                    const hash = getHash(confirmations[i]);
                    const metricinfo = await fetchContractData(abiMaster, ADDR_MASTER, 'getMetricaInfo', [hash])
                    const oracleinfo = await fetchContractData(abiMaster, ADDR_MASTER, 'getOracleInfo', [metricinfo.oracle])
                    let info = {
                        name: metricinfo.name,
                        oracle: metricinfo.oracle,
                        label: metricinfo.label,
                        oracleLabel: oracleinfo['1']
                    }
                    if(profile !== this.currentProfile) {
                        return
                    }
                    this.confirmations.push(info)
                }
            }
            if(Number(lengthMetricNames)) {
                const nameMetrics = await fetchContractData(abiProfileContract, profile, 'getSliceMetricNames', [0, lengthMetricNames]);
                for(let i = 0; i < nameMetrics.length; i++) {
                    const hash = getHash(nameMetrics[i]);
                    const metricinfo = await fetchContractData(abiMaster, ADDR_MASTER, 'getMetricaInfo', [hash]);
                    const oracleinfo = await fetchContractData(abiMaster, ADDR_MASTER, 'getOracleInfo', [metricinfo.oracle]);
                    let info = {
                        name: metricinfo.name,
                        oracle: metricinfo.oracle,
                        label: metricinfo.label,
                        oracleLabel: oracleinfo['1']
                    }
                    if(profile !== this.currentProfile) {
                        return;
                    }
                    this.nameMetrics.push(info);
                    const metricType = await fetchContractData(abiDataRouter, ADDR_DATA_ROUTER, 'getTypeMetricByName', [nameMetrics[i]]);
                    const getMethod = methodsByType[metricType];
                    const result = await fetchContractData(abiDataRouter, ADDR_DATA_ROUTER, getMethod, [profile, nameMetrics[i]]);
                    const converted = converter(result, metricType);
                    if(nameMetrics[i] === 'PROFILE_PICTURE') {
                        this.linkPhoto = converted;
                        continue;
                    }
                    if(profile !== this.currentProfile) {
                        return;
                    }
                    this.metrics.push({
                        result: converted,
                        label: metricinfo.label
                    });
                }  
            }
        },
        async handleSelectProfile(address) {
            this.openSetting = false;
            this.confirmations = [];
            this.nameMetrics = [];
            this.metrics = [];
            this.linkPhoto = '';
            this.currentProfile = address; 
            await this.getProfileInfo();
        },
        handleSelectTab(iTab) {
            this.currentTab = iTab
        },
        async handleCreateProfile() {
            if(!this.newProfile) {
                this.$vToastify.info('Fill full name', 'Validation warning')
            } else {
                const data = {
                    method: 'createNewProfileContract',
                    params: [this.newProfile]
                }
                await this.SEND_DATA(data);
                this.handleCloseCreateWindow();
                const user = await getFirstAddress();
                const lengthBefore = await fetchContractData(abiProfileRouter, ADDR_PROFILE_ROUTER, 'getLengthProfilesArray', [user]);
                let pingTimer; 
                let loopCounter = 0;
                pingTimer = setInterval(async () => {
                    loopCounter++;
                    const lengthAfter = await fetchContractData(abiProfileRouter, ADDR_PROFILE_ROUTER, 'getLengthProfilesArray', [user]);
                    if (lengthBefore < lengthAfter || loopCounter > 15) {
                        if(lengthBefore < lengthAfter) {
                            this.$vToastify.success('Data updated successfully', '')
                        } else {
                            this.$vToastify.warning('Maybe something went wrong, track the transaction on etherscan', '')
                        } 
                        clearInterval(pingTimer);
                        await this.GET_PROFILES_ARRAY()
                        await this.GET_TRANSMITTED_PROFILES()
                        await this.GET_ACCEPTED_PROFILES()
                        const lastProfile = this.PROFILES_ARRAY[this.PROFILES_ARRAY.length - 1];
                        await this.handleSelectProfile(lastProfile);
                        await this.getNamesByProfiles();
                    }
                }, 2000)
            }
        },
        handleOpenSetting() {
            this.openSetting = !this.openSetting;
        },
        async handleCancelTransferTransmittedProfile() {
            const user = await getFirstAddress();
            const lengthBefore = (await fetchContractData(abiProfileRouter, ADDR_PROFILE_ROUTER, 'getTransmittedProfiles', [user])).length;
            const data = {
                method: 'cancelTransferProfileOwnership',
                params: [this.currentProfile]
            }
            await this.SEND_DATA(data);
            let pingTimer; 
            let loopCounter;
            pingTimer = setInterval(async () => {
                loopCounter++;
                const lengthAfter = (await fetchContractData(abiProfileRouter, ADDR_PROFILE_ROUTER, 'getTransmittedProfiles', [user])).length;
                if (lengthBefore > lengthAfter || loopCounter > 10) {
                    if(lengthBefore > lengthAfter) {
                        this.$vToastify.success('Data updated successfully', '')
                    } else {
                        this.$vToastify.warning('Maybe something went wrong, track the transaction on etherscan', '')
                    } 
                    clearInterval(pingTimer);
                    await this.GET_PROFILES_ARRAY()
                    await this.GET_TRANSMITTED_PROFILES()
                    await this.GET_ACCEPTED_PROFILES()
                    await this.getNamesByProfiles();
                }
            }, 2000)
        },
        async handleCancelTransferAcceptedProfile() {
            const user = await getFirstAddress();
            const lengthBefore = (await fetchContractData(abiProfileRouter, ADDR_PROFILE_ROUTER, 'getAcceptedProfiles', [user])).length;
            const data = {
                method: 'cancelTransferProfileOwnership',
                params: [this.currentProfile]
            }
            await this.SEND_DATA(data);
            let pingTimer; 
            let loopCounter;
            pingTimer = setInterval(async () => {
                loopCounter++;
                const lengthAfter = (await fetchContractData(abiProfileRouter, ADDR_PROFILE_ROUTER, 'getAcceptedProfiles', [user])).length;
                if (lengthBefore > lengthAfter || loopCounter > 10) {
                    if(lengthBefore > lengthAfter) {
                        this.$vToastify.success('Data updated successfully', '')
                    } else {
                        this.$vToastify.warning('Maybe something went wrong, track the transaction on etherscan', '')
                    } 
                    clearInterval(pingTimer);
                    await this.GET_PROFILES_ARRAY()
                    await this.GET_TRANSMITTED_PROFILES()
                    await this.GET_ACCEPTED_PROFILES()
                    this.currentProfile = this.PROFILES_ARRAY[0];
                    await this.getProfileInfo();
                    await this.getNamesByProfiles();
                }
            }, 2000)
        },
        async getNamesByProfiles() {
            if(this.PROFILES_ARRAY.length) {
                for(let i = 0; i < this.PROFILES_ARRAY.length; i++) {
                    const name = await fetchContractData(abiProfileContract, this.PROFILES_ARRAY[i], 'fullName', []);
                    this.profilesAndNames.push({
                        fullName: name,
                        address: this.PROFILES_ARRAY[i]
                    })
                }
            }
            if(this.ACCEPTED_PROFILES.length) {
                for(let i = 0; i < this.ACCEPTED_PROFILES.length; i++) {
                    const name = await fetchContractData(abiProfileContract, this.ACCEPTED_PROFILES[i], 'fullName', []);
                    this.acceptedProfilesAndNames.push({
                        fullName: name,
                        address: this.ACCEPTED_PROFILES[i]
                    })
                }
            }
        }
    },
    computed: {
        ...mapGetters(['PROFILES_ARRAY','TRANSMITTED_PROFILES', 'ACCEPTED_PROFILES']),
        valueForTranslate() {
            if (this.currentTab === 1) {
                return '0';
            } else if (this.currentTab === 2) {
                return '250'
            } else {
                return '499'
            }
        },
        isTransmittedProfile() {
            return this.TRANSMITTED_PROFILES.includes(this.currentProfile);
        },
        isAcceptedProfile() {
            return this.ACCEPTED_PROFILES.includes(this.currentProfile);
        }
    }
}
</script>

<style scoped>
.premodal {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    z-index: 1000;
}
.dialog {
    border: 2px solid rgba(0, 0, 0, 0.175);
    border-radius: 10px;
    width: 900px;
    margin-top: 300px;
    background: white;
}
.pointer {
    cursor: pointer;
}
.pointer__tab {
    cursor: pointer;
}
.currentProfile {
    background-color: #6c757d;
    color: #EAF4F4;
}
.pointer:hover {
    background-color: #EAF4F4;
}
.profile {
